<template>
<nav>
  <a href="/addImage"  class="add">Add Image</a>
  <div id="map"></div>
  <img v-bind:src="'data:image/jpeg;base64,'+images[0].url" alt="image"/>


</nav>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      error: false,
      coords: [],
      images: []
    }
  },
  mounted() {
    this.$nextTick(function () {
      axios.get('/api/image?page='+0).then(response => {
        this.images=response.data.data
      }).catch((error) => {
        console.log(error.response.status)
      })

      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });

        const objectManager = new ymaps.ObjectManager({
          clusterize: true,
          // gridSize: 32,
          // clusterDisableClickZoom: true
          geoObjectOpenBalloonOnClick: false,
          clusterOpenBalloonOnClick: false
        });
        objectManager.objects.options.set('preset', 'islands#greenDotIcon');
        objectManager.clusters.options.set('preset', 'islands#greenClusterIcons');
        myMap.geoObjects.add(objectManager);
        for (let i = 0; i <this.images.length ; i++) {
          const image = JSON.stringify({type: 'Feature', id: this.images[i].id, geometry: {type: 'Point',
              coordinates: [this.images[i].lat,this.images[i].lng]}, properties: {ballooonContent: '<font size=3><b><a target=\'_blank\' href=\'https://yandex.ru\'>    </a></b></font>'} })
          objectManager.add(image)
          console.log(image)
        }
        function onObjectEvent (e) {
          var objectId = e.get('objectId');
          if (e.get('type') == 'mouseenter') {
            //  setObjectOptions     " ".
            objectManager.objects.setObjectOptions(objectId, {
              preset: 'islands#yellowIcon'
            });
          } else {
            objectManager.objects.setObjectOptions(objectId, {
              preset: 'islands#blueIcon'
            });
          }
        }

        function onClusterEvent (e) {
          var objectId = e.get('objectId');
          if (e.get('type') == 'mouseenter') {
            objectManager.clusters.setClusterOptions(objectId, {
              preset: 'islands#yellowClusterIcons'
            });
          } else {
            objectManager.clusters.setClusterOptions(objectId, {
              preset: 'islands#blueClusterIcons'
            });
          }
        }

        objectManager.objects.events.add(['mouseenter', 'mouseleave'], onObjectEvent);
        objectManager.clusters.events.add(['mouseenter', 'mouseleave'], onClusterEvent);

      })





    })
  },
  methods: {

  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

.info_paragraph {
  font-size: 25px;
}
.add {
  color: blue;
  text-decoration: none;
}
</style>
